import { ApiActions, FormActions, InitialStateType } from 'interface/interface';
import React, { createContext, useReducer, Dispatch, ReactNode } from 'react';
import { formReducer, apiReducer } from './newReducer';

const initialState = {
  forms: [],
  apiFilter: {
    status: '',
    gender: '',
    species: '',
    search: '',
    currentPage: 1,
    totalPage: 1,
    characters: [],
    cardsPerPage: 10,
  },
};

const AppContext = createContext<{
  state: InitialStateType;
  dispatch: Dispatch<FormActions | ApiActions>;
}>({
  state: initialState,
  dispatch: () => null,
});

const mainReducer = ({ forms, apiFilter }: InitialStateType, action: FormActions | ApiActions) => ({
  forms: formReducer(forms, action),
  apiFilter: apiReducer(apiFilter, action),
});

const AppProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [state, dispatch] = useReducer(mainReducer, initialState);

  return <AppContext.Provider value={{ state, dispatch }}>{children}</AppContext.Provider>;
};

export { AppProvider, AppContext };
