import React from 'react';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Link, useLocation } from 'react-router-dom';

export default function CharacterPage() {
  const location = useLocation();
  return (
    <div className="character-page-wrapper">
      <div className="character-page-nav">
        <Link to="/api">
          <ArrowBackIcon />
          Back
        </Link>
      </div>
      <h2>{location.state.value.name}</h2>
      <div className="character-page">
        <div>
          <img src={location.state.value.image} alt={location.state.value.name} />
        </div>
        <ul>
          <li>Name: {location.state.value.name}</li>
          <li>Gender: {location.state.value.gender}</li>
          <li>Status: {location.state.value.status}</li>
          <li>Species: {location.state.value.species}</li>
          <li>Location: {location.state.value.origin?.name}</li>
          <li>Type: {location.state.value.type || 'unknown'}</li>
        </ul>
      </div>
    </div>
  );
}
