import { FormControl, InputLabel, NativeSelect, Pagination } from '@mui/material';
import Filter from 'components/filter';
import Header from 'components/header';
import Loading from 'components/loading';
import NothingHere from 'components/nothingHere';
import RenderApiCards from 'components/renderApiCards';
import SearchBar from 'components/searchBar';
import { AppContext } from 'contexts/newContext';
import { URL } from 'data/url';
import { Types } from 'interface/interface';
import React, { useEffect, useState } from 'react';
import apiResult from 'service/api';

export default function ApiPage() {
  const { state, dispatch } = React.useContext(AppContext);
  const { search, status, gender, species, currentPage, totalPage, characters, cardsPerPage } =
    state.apiFilter;
  const [isPending, setIsPending] = useState(true);

  function handlerOnChange(event: React.ChangeEvent<HTMLInputElement>) {
    dispatch({
      type: Types.SaveSearch,
      payload: {
        search: event.target.value,
      },
    });
  }

  function fetchRequest(
    URL: string,
    search: string,
    status: string,
    gender: string,
    species: string,
    currentPage: number
  ) {
    setIsPending(true);
    setTimeout(async () => {
      const data = await apiResult(URL, search, status, gender, species, currentPage);
      dispatch({
        type: Types.TotalPage,
        payload: {
          totalPage: data.error ? 0 : data.info.pages - 1,
        },
      });
      data.error
        ? dispatch({
            type: Types.SaveCharacters,
            payload: {
              characters: [],
            },
          })
        : dispatch({
            type: Types.SaveCharacters,
            payload: {
              characters: data.results,
            },
          });
      setIsPending(false);
    }, 500);
  }

  useEffect(() => {
    if (cardsPerPage === 10) {
      const thisPage = Math.ceil(currentPage / 2);
      fetchRequest(URL, search, status, gender, species, thisPage);
    }
    if (cardsPerPage === 20) {
      let thisPage = 1;
      totalPage < currentPage ? (thisPage = Math.ceil(currentPage / 2)) : (thisPage = currentPage);
      fetchRequest(URL, search, status, gender, species, thisPage);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [status, gender, species, currentPage, cardsPerPage]);

  async function handlerOnClick() {
    await dispatch({
      type: Types.ChangePage,
      payload: {
        currentPage: 1,
      },
    });
    fetchRequest(URL, search, status, gender, species, 1);
  }

  function handleKeyDown(event: React.KeyboardEvent) {
    if (event.key === 'Enter') {
      handlerOnClick();
    }
  }

  return (
    <>
      <Header />
      <h2>API Page</h2>
      <SearchBar
        search={search}
        handlerOnChange={handlerOnChange}
        handleKeyDown={(e: React.KeyboardEvent) => handleKeyDown(e)}
        handlerOnClick={handlerOnClick}
      />
      <div className="wrapper-cards">
        {
          <>
            <Filter />
            <div className="per-page">
              <div className="per-page-content">
                <FormControl fullWidth>
                  <InputLabel variant="standard" htmlFor="uncontrolled-native">
                    Cards per page:
                  </InputLabel>
                  <NativeSelect
                    value={cardsPerPage}
                    onChange={(event) => {
                      dispatch({
                        type: Types.SetCardsPerPage,
                        payload: {
                          cardsPerPage: +event.target.value,
                        },
                      }),
                        dispatch({
                          type: Types.ChangePage,
                          payload: {
                            currentPage: 1,
                          },
                        });
                    }}
                  >
                    <option value={10}>10</option>
                    <option value={20}>20</option>
                  </NativeSelect>
                </FormControl>
              </div>
            </div>
          </>
        }
        {isPending ? (
          <Loading />
        ) : characters.length ? (
          <RenderApiCards
            popupContent={
              cardsPerPage === 10 && currentPage % 2 == 0
                ? characters.slice(cardsPerPage, 20)
                : characters.slice(0, cardsPerPage)
            }
          />
        ) : (
          <NothingHere />
        )}
        {
          <div className="pagination">
            <Pagination
              count={cardsPerPage === 10 ? totalPage * 2 : totalPage}
              page={currentPage}
              onChange={async (_, num) =>
                await dispatch({
                  type: Types.ChangePage,
                  payload: {
                    currentPage: num,
                  },
                })
              }
            />
          </div>
        }
      </div>
    </>
  );
}
