const apiResult = async (
  url: string,
  search: string,
  status = '',
  gender = '',
  species = '',
  page: number
) => {
  const response = await fetch(
    `${url}page=${page}&name=${search}&status=${status}&gender=${gender}&species=${species}`
  );
  const data = await response.json();
  return data;
};
export default apiResult;
