import FormCards from 'components/formCards';
import Header from 'components/header';
import { AppContext } from 'contexts/newContext';
import { FormType, Types } from 'interface/interface';
import React, { ChangeEvent, useContext, useState } from 'react';
import { useForm } from 'react-hook-form';

export default function Form() {
  const { state, dispatch } = useContext(AppContext);
  const [submitButton, setSubmitButton] = useState(true);
  const [checkErrorForm, setCheckErrorForm] = useState(false);
  const [sendForm, setSendFrom] = useState(false);
  const [cardImage, setCardImage] = useState<Blob>();
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<FormType>({
    defaultValues: {
      radio: 'Male',
    },
    mode: 'onChange',
    shouldFocusError: true,
  });

  function handleChange() {
    !checkErrorForm && setSubmitButton(false);
    const allMassage = Array.from(document.querySelectorAll('.error-message')).length - 1;
    !allMassage && setSubmitButton(false);
    setSendFrom(false);
  }

  const onSubmit = (data: FormType) => {
    data.image = cardImage;
    dispatch({
      type: Types.Create,
      payload: {
        name: data.name,
        data: data.data,
        select: data.select,
        check: data.check,
        radio: data.radio,
        image: data.image,
      },
    });
    setCheckErrorForm(false);
    setSubmitButton(true);
    setSendFrom(true);
    reset();
  };
  const onError = () => {
    setSubmitButton(true);
  };
  const handleSetImage = (event: ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files as FileList;
    const img = file[0] as Blob;
    setCardImage(img);
  };
  return (
    <>
      <Header />
      <h2>Form</h2>
      <form
        id="form"
        className="form"
        name="control-ref"
        onSubmit={handleSubmit(onSubmit, onError)}
        onChange={handleChange}
      >
        <label>Name*</label>
        <input
          type="text"
          role="name"
          placeholder="Name..."
          {...register('name', {
            required: 'Name is required field!!!',
          })}
        />
        {errors.name && <div style={{ color: 'red' }}>{errors.name.message}</div>}
        <label>Birthday*</label>
        <input
          type="date"
          role="data"
          {...register('data', {
            required: 'Date is required field!!!',
          })}
        />
        {errors.data && <div style={{ color: 'red' }}>{errors.data.message}</div>}
        <label>Department</label>
        <select {...register('select')}>
          <option value="Logistic">Logistic</option>
          <option value="Developer">Developer</option>
          <option value="Marketing">Marketing</option>
        </select>
        <label>Gender</label>
        <div className="input-field">
          <input {...register('radio')} type="radio" value="Male" />
          <span>Male</span>
        </div>
        <div className="input-field">
          <input {...register('radio')} type="radio" value="Female" />
          <span>Female</span>
        </div>
        <label>Relocate</label>
        <div className="input-field">
          <input {...register('check')} type="checkbox" value="Yes" />
          <span>Yes</span>
        </div>
        <label>Avatar</label>
        <input
          {...register('image', {
            required: 'Please select your image!',
          })}
          type="file"
          role="image"
          onChange={(e) => handleSetImage(e)}
        />
        {errors.image && <div style={{ color: 'red' }}>{errors.image.message}</div>}
        <input
          className="form-button"
          type="submit"
          role="submit"
          value="Send"
          disabled={submitButton}
        />
        {sendForm && <p className="form-send">Your form has been submitted successfully!</p>}
      </form>
      <FormCards cards={state.forms} />
    </>
  );
}
