import { ApiActions, Character, FormActions, FormType, Types } from '../interface/interface';

export const formReducer = (state: FormType[], action: FormActions | ApiActions) => {
  switch (action.type) {
    case Types.Create:
      return [
        ...state,
        {
          name: action.payload.name,
          data: action.payload.data,
          select: action.payload.select,
          check: action.payload.check,
          radio: action.payload.radio,
          image: action.payload.image,
        },
      ];
    default:
      return state;
  }
};
export const apiReducer = (
  state: {
    status: string;
    gender: string;
    species: string;
    search: string;
    currentPage: number;
    totalPage: number;
    characters: Character[];
    cardsPerPage: number;
  },
  action: FormActions | ApiActions
) => {
  switch (action.type) {
    case Types.AddStatus:
      return {
        ...state,
        status: action.payload.status,
      };
    case Types.AddGender:
      return {
        ...state,
        gender: action.payload.gender,
      };
    case Types.AddSpecies:
      return {
        ...state,
        species: action.payload.species,
      };
    case Types.SaveSearch:
      return {
        ...state,
        search: action.payload.search,
      };
    case Types.ChangePage:
      return {
        ...state,
        currentPage: action.payload.currentPage,
      };
    case Types.TotalPage:
      return {
        ...state,
        totalPage: action.payload.totalPage,
      };
    case Types.SaveCharacters:
      return {
        ...state,
        characters: action.payload.characters,
      };
    case Types.SetCardsPerPage:
      return {
        ...state,
        cardsPerPage: action.payload.cardsPerPage,
      };
    default:
      return state;
  }
};
