import React from 'react';

export default function FormCard({ ...props }) {
  return (
    <div className="form-card">
      <div className="form-card-header">
        {props.image && <img className="form-card-avatar" src={URL.createObjectURL(props.image)} />}
        <div className="form-card-content">
          <h3>{props.name}</h3>
          <ul>
            <li>
              <span>Birthday:</span>
              {props.data}
            </li>
            <li>
              <span>Department:</span>
              {props.select}
            </li>
            <li>
              <span>Gender: </span>
              {props.radio}
            </li>
            <li>
              <span>Relocate: </span>
              {props.check || 'no'}
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
