export interface Character {
  id?: number;
  name?: string;
  status?: string;
  species?: string;
  type?: string;
  gender?: string;
  origin?: {
    name?: string;
    url?: string;
  };
  location?: {
    name: string;
    url: string;
  };
  image?: string;
  episode?: string[];
  url?: string;
  created?: string;
}

export interface Props {
  changeContent: (character?: Character[]) => Promise<void>;
  popupContent?: Character[] | undefined;
}

export type FormType = {
  name: string;
  data: string;
  select: string;
  check: string;
  radio: string;
  image?: Blob;
};

export type InitialStateType = {
  forms: FormType[];
  apiFilter: {
    status: string;
    gender: string;
    species: string;
    search: string;
    currentPage: number;
    totalPage: number;
    characters: Character[];
    cardsPerPage: number;
  };
};

type ActionMap<M extends { [index: string]: unknown }> = {
  [Key in keyof M]: M[Key] extends undefined
    ? {
        type: Key;
      }
    : {
        type: Key;
        payload: M[Key];
      };
};

export enum Types {
  Create = 'CREATE_FORM',
  AddStatus = 'ADD_STATUS_FILTER',
  AddGender = 'ADD_GENDER_FILTER',
  AddSpecies = 'ADD_SPECIES_FILTER',
  SaveSearch = 'SAVE_SEARCH',
  ChangePage = 'CHANGE_PAGE',
  TotalPage = 'TOTAL_PAGE',
  SaveCharacters = 'SAVE_CHARACTERS',
  SetCardsPerPage = 'SET_CARDS_PER_PAGE',
}

export type FormPayload = {
  [Types.Create]: {
    name: string;
    data: string;
    select: string;
    check: string;
    radio: string;
    image?: Blob;
  };
};

export type FormActions = ActionMap<FormPayload>[keyof ActionMap<FormPayload>];

export type ApiActions = ActionMap<ApiPayload>[keyof ActionMap<ApiPayload>];

export type ApiPayload = {
  [Types.AddStatus]: {
    status: string;
  };
  [Types.AddGender]: {
    gender: string;
  };
  [Types.AddSpecies]: {
    species: string;
  };
  [Types.SaveSearch]: {
    search: string;
  };
  [Types.ChangePage]: {
    currentPage: number;
  };
  [Types.TotalPage]: {
    totalPage: number;
  };
  [Types.SaveCharacters]: {
    characters: Character[];
  };
  [Types.SetCardsPerPage]: {
    cardsPerPage: number;
  };
};
