import React from 'react';
import FormCard from './formCard';

interface IFormCard {
  name: string;
  data: string;
  select: string;
  check: string;
  radio: string;
  image?: Blob;
}

export default function FormCards({ ...props }) {
  return (
    <div className="form-wrapper-cards">
      {props.cards.map((card: IFormCard, index: number) => (
        <FormCard {...card} key={index} />
      ))}
    </div>
  );
}
