import { AppContext } from 'contexts/newContext';
import { Types } from 'interface/interface';
import React from 'react';

export default function Filter() {
  const { state, dispatch } = React.useContext(AppContext);
  return (
    <div className="filter-section">
      <h2>Filter Characters</h2>
      <div
        className="filter-section-content"
        onChange={() => {
          dispatch({
            type: Types.ChangePage,
            payload: {
              currentPage: 1,
            },
          });
        }}
      >
        <h3>Status:</h3>
        <ul>
          <li>
            <label>
              <input
                type="radio"
                name="status"
                value="Alive"
                defaultChecked={state.apiFilter.status === 'Alive'}
                onClick={() => {
                  dispatch({
                    type: Types.AddStatus,
                    payload: {
                      status: 'Alive',
                    },
                  });
                }}
              />
              Alive
            </label>
          </li>
          <li>
            <label>
              <input
                type="radio"
                name="status"
                value="Dead"
                defaultChecked={state.apiFilter.status === 'Dead'}
                onClick={() => {
                  dispatch({
                    type: Types.AddStatus,
                    payload: {
                      status: 'Dead',
                    },
                  });
                }}
              />
              Dead
            </label>
          </li>
          <li>
            <label>
              <input
                type="radio"
                name="status"
                value=""
                defaultChecked={state.apiFilter.status === ''}
                onClick={() => {
                  dispatch({
                    type: Types.AddStatus,
                    payload: {
                      status: '',
                    },
                  });
                }}
              />
              All
            </label>
          </li>
        </ul>
        <h3>Gender:</h3>
        <ul>
          <li>
            <label>
              <input
                type="radio"
                name="gender"
                value="Male"
                defaultChecked={state.apiFilter.gender === 'Male'}
                onClick={() => {
                  dispatch({
                    type: Types.AddGender,
                    payload: {
                      gender: 'Male',
                    },
                  });
                }}
              />
              Male
            </label>
          </li>
          <li>
            <label>
              <input
                type="radio"
                name="gender"
                value="Female"
                defaultChecked={state.apiFilter.gender === 'Female'}
                onClick={() => {
                  dispatch({
                    type: Types.AddGender,
                    payload: {
                      gender: 'Female',
                    },
                  });
                }}
              />
              Female
            </label>
          </li>
          <li>
            <label>
              <input
                type="radio"
                name="gender"
                value=""
                defaultChecked={state.apiFilter.gender === ''}
                onClick={() => {
                  dispatch({
                    type: Types.AddGender,
                    payload: {
                      gender: '',
                    },
                  });
                }}
              />
              All
            </label>
          </li>
        </ul>
        <h3>Species:</h3>
        <ul>
          <li>
            <label>
              <input
                type="radio"
                name="species"
                value="Human"
                defaultChecked={state.apiFilter.species === 'Human'}
                onClick={() => {
                  dispatch({
                    type: Types.AddSpecies,
                    payload: {
                      species: 'Human',
                    },
                  });
                }}
              />
              Human
            </label>
          </li>
          <li>
            <label>
              <input
                type="radio"
                name="species"
                value="Alien"
                defaultChecked={state.apiFilter.species === 'Alien'}
                onClick={() => {
                  dispatch({
                    type: Types.AddSpecies,
                    payload: {
                      species: 'Alien',
                    },
                  });
                }}
              />
              Alien
            </label>
          </li>
          <li>
            <label>
              <input
                type="radio"
                name="species"
                value=""
                defaultChecked={state.apiFilter.species === ''}
                onClick={() => {
                  dispatch({
                    type: Types.AddSpecies,
                    payload: {
                      species: '',
                    },
                  });
                }}
              />
              All
            </label>
          </li>
        </ul>
      </div>
    </div>
  );
}
